import React from 'react';
import axios from 'axios';
import 'react-bootstrap';

const apiKey = 'a2cc0c1ee600479eac7475b0e8d8de38';
const liStyle = {
    listStyleType: 'none',
    width: '100%',
    overflowY: 'scroll',
    padding: '10px 10px', 
    boxSizing: 'content-box',
    border: 'solid red 2px',
    borderStyle: 'none none solid none'
}

const ulStyle = {
    height: '100vh',
    overflowY: 'scroll'
}

export class Main extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            newsApp: {articles: []}
        };
    }

    componentDidMount() {
        axios.get(`http://newsapi.org/v2/everything?q=programming&sortBy=publishedAt&apiKey=${apiKey}`)
        .then(res => {
            this.setState({ newsApp: res.data });
        })
        .catch(err => {
            return err;
          });
    }

    render() {
        return <ul style={ulStyle}>{this.state.newsApp.articles.map((wog, index) => 
        <li style={{...liStyle, color: 'black'}}
            key={index}>
           <h3>{wog.title}</h3>
            <h4><br />{wog.author}</h4>
            <p><br />{wog.description}</p>
        </li>) }
        </ul>;
    }
} 