import React from 'react';
import TextLoop from "react-text-loop";
import pfp from './images/linkedinpfp1.png';
import './About.css';

const divStyle = {
    margin: 'auto',
    width: '50%',
    textAlign: 'center',
    height: '70vh'
};

export class About extends React.Component {

    render() {
        return <div style={divStyle}>
        <h1>Cody Silas Hill</h1>
        <br />
        <img src={pfp} alt="Cody Profile Pic" className="pfp"/>
        <h2 style={{textAlign: 'left'}}>
          Proficient in... &nbsp;&nbsp;
          <TextLoop
          springConfig={{ stiffness: 130, damping: 10 }}
          adjustingSpeed={200}>
            <span>  JavaScript/HTML5/CSS3</span>
            <span> React JS </span>
            <span> PHP </span>
            <span> Java </span>
            <span> Python 3 </span>
            <span> Data Structures </span>
            <span> Español </span>
            <span> 日本語 </span>
          </TextLoop>{" "}
        </h2>
      </div>;
    }
}