import React from 'react';
import ReactDOM from 'react-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';


const  headerStyle = {
    color: 'blue',
    background: '#' + Math.floor(Math.random()*16777215).toString(16),
    transition: 'all 1s ease-out',
};

const colors = ['#5946B2', '#916FD9', '#B19CDA', '#00B1E8', '#0569B3'];
let indexColor = 0;

export class Header extends React.Component {      

    constructor(props) {
        super(props);
        this.state = {
            logo: 'logo',
            hexVal: '#5946B2',
            index: 0
        };
    }

    componentDidMount() {

        this.timerID = setInterval(
            () => this.changeColor(),
            5000
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    changeColor() {
        indexColor++;
        if (indexColor > (colors.length - 1)) {
        indexColor = 0;
        this.setState({
            index: indexColor
        })
    } else {
        this.setState({
            index: indexColor
        })
      }
    }

    

    render() {
        return <header style={{...headerStyle, color: 'blue',
        background: colors[this.state.index]}}>
            <h1>Welcome to {this.props.name}'s Website</h1>
            <h2>NavBar Here</h2>
        </header>;

    }
}