import React from 'react';

const navList = ["home", "news", "contact", "about"];
const listItems = navList.map((name) => 
  <li style = {{display: 'inline', float: 'left'}}>
  <a style= {{display: 'block', padding: '2em', backgroundColor: '#dddddd'}}
  href={"#/" + name} >
      {name}</a>
      </li>
);
const ulStyle = {
    listStyleType: 'none',
    margin: 'auto',
    padding: 'auto',
    position: 'fixed',
    bottom: 0,
    width: '100%',
    marginTop: 0,
    height: '5em',
    zIndex: 999
}

export class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Disko'
        };
    }

    render() {
        return <div style={{width: '100%'}}> 
        <ul style={ulStyle}>
              {listItems}
            </ul>
            </div>;
        
    }
}